import type {ReactNode} from "react";

import {cls} from "../../../util/css";

import * as pageFooterStyle from "./page-footer.scss";

interface PageFooterPropsType {
    readonly children?: ReactNode;

    readonly className?: string;
}
export function PageFooter(props: PageFooterPropsType): JSX.Element {
    const {children, className} = props;

    return <div className={cls(pageFooterStyle.page_footer, className)}>{children}</div>;
}
