import type {ReactNode} from "react";

import {cls} from "../../../util/css";

import * as pageContentStyle from "./page-content.scss";

interface PageContentPropsType {
    readonly children: ReactNode;

    readonly className?: string;
    readonly contentClassName?: string;
    readonly hasBorder?: boolean;
}

export function PageContent(props: PageContentPropsType): JSX.Element {
    const {children, hasBorder, className, contentClassName} = props;

    return (
        <div className={cls(pageContentStyle.page_content, className)}>
            <div
                className={
                    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions
                    cls(hasBorder ? pageContentStyle.page_content__border : "", contentClassName)
                }
            >
                {children}
            </div>
        </div>
    );
}
