/* global HTMLImageElement */

import {cls} from "../../../util/css";
import {fontSmallImage} from "../image/font-image";

import * as textStyle from "./text.scss";

interface SmallTextPropsType {
    readonly className?: string;
    readonly scale?: number;
    readonly text: string;
}

const localImageMap: Record<string, HTMLImageElement> = {...fontSmallImage};

export function SmallText(props: SmallTextPropsType): JSX.Element {
    const {text, className, scale = 1} = props;

    return (
        <div className={cls(textStyle.text_wrapper, className)}>
            {text.split("").map<JSX.Element>((letter: string, index: number): JSX.Element => {
                const imageUrl: string = localImageMap[letter].src || fontSmallImage[" "].src;

                return (
                    <img
                        alt={letter}
                        className={textStyle.text_item}
                        height={`${7 * scale}px`}
                        key={`${imageUrl}-${index.toString(16)}`}
                        src={imageUrl}
                    />
                );
            })}
        </div>
    );
}
