    INSTRUCTION__04:
        "Olika typer av terräng påverkar truppens förmåga att röra sig, attackera och försvara sig. Till exempel ger bergen en stridande trupper skyddspoäng men sänker hastigheten. När du flyttar kursorn över sådana platser, kommer deras egenskaper att visas längst ner på skärmen.",
