    INSTRUCTION__09:
        'För att attackera en fiende måste du flytta din krigare in i attacklinjen och välja "Attackera". Om det finns flera fiender inom räckhåll, välj vilken fiende du vill attackera. Framgången av en attack bestäms av krigaresegenskaper hos de stridande och av platsen.',
    INSTRUCTION__10:
        "Endast befälhavaren kan inta slottet. När slottet är ockuperat, kan du köpa nya trupper med guld. Befälhavaren behöver inte stanna i slottet för att köpa soldater.",
