        'Du kan ockupera en byggnad genom att flytta en soldat eller en befälhavare till den och välja "Ockupera". När byggnaden är upptagen ändrar den färg. Om byggnaden är skadad måste du reparera den innan den kan tas i bruk.',
    INSTRUCTION__12:
        "När byggnaden är upptagen, kommer den att föra in guld. Ju fler byggnader ni tar, desto mer pengar får ni.",
